.videodata {
  margin: 0 auto;
  padding: 122px 0;

  text-align: center;

  background: linear-gradient(
  to bottom,
  $black-dark 706px,
  $white 706px,
  $white 98px
  );

  @media (max-width: $tablet-width-only) {
    padding: 70px 0 50px;

    background: linear-gradient(
    to bottom,
    $black-dark 413px,
    $white 413px,
    $white 858px
    );
  }

  @media (max-width: $mobile-width-only) {
    padding: 86px 0 10px;

    background: linear-gradient(
    to bottom,
    $black-dark 386px,
    $white 386px,
    $white 29px
    );
  }
}

.videodata h2 {
  @include font(30px, 30px, 600);

  margin: 0 auto;

  color: $white;
  letter-spacing: -0.005em;

  @media (max-width: $tablet-width-only) {
    @include font(24px, 30px, 600);
  }
}

.videodata p {
  @include font(16px, 28px, 300);

  width: 30%;
  margin: 0 auto;
  padding-top: 20px;

  color: $white;
  letter-spacing: -0.005em;

  opacity: 0.7;

  @media (max-width: $tablet-width-only) {
    width: 100%;
    max-width: 440px;
  }

  @media (max-width: $mobile-width-only) {
    padding-top: 19px;
  }
}

.videodata__video {
  position: relative;

  max-width: 930px;
  margin: 0 auto;
  padding-top: 55px;

  @media (max-width: $tablet-width-only) {
    max-width: 503px;
    padding-top: 40px;
    padding-left: 0;
  }

  @media (max-width: $mobile-width-only) {
    width: 288px;
    max-width: 288px;
    margin: 0 auto;
    padding-top: 53px;
  }
}

.videodata img {
  display: block;
  width: 100%;
  height: auto;
}

.videodata button {
  position: absolute;
  top: 50%;
  left: 50%;

  background: none;
  border: none;
  transform: translate(-50%, 0);

  cursor: pointer;

  @media (max-width: $tablet-width-only) {
    transform: translate(-50%, -27%);
  }

  @media (max-width: $mobile-width-only) {
    transform: translate(-50%, -11%);
  }
}

.videodata svg {
  color: $cyan;

  transition: 0.6s;

  @media (max-width: $tablet-width-only) {
    width: 90px;
    height: auto;
  }

  @media (max-width: $mobile-width-only) {
    width: 65px;
    height: auto;
  }

  &:hover {
    color: $gray;

    transition: 0.6s;
  }

  &:active {
    color: $black;

    transition: 0.6s;
  }
}
